h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --main-text-font: "DM Sans", sans-serif;
  --anim-fast: .25s ease-in;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--title-color);
  background-color: var(--bg-color);
  font-display: optional;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

body.light {
  color: #111;
  background-color: #f6f6f6;
}

body.dark {
  color: #fff;
  background-color: #202024;
}

body.light header {
  color: #111;
  background-color: #fff;
}

body.dark header {
  color: #fff;
  background-color: #111;
}

[data-theme="light"] {
  --bg-color: #f6f6f6;
  --header-white-color: #fff;
  --black-text-color: #111;
  --white-color: #fff;
  --title-color: #111;
  --accent-blue-color: #4f2ee8;
  --accent-yellow-color: #eac645;
  --skroll-color: #e0e0e0;
  --modal-signup-color: #f3f3f3;
  --modal-font-color: #111;
  --main-font-color: #9a9a9a;
  --font-card-color: #f6f6f6;
  --light-font-color: #1119;
  --border-color: #4f2ee866;
  --grey-text-color: #b4afaf;
  --accent-color-filter: #4f2ee8;
  --amazon-image-filter: grayscale(100%);
  --loader-book-color: #4f2ee8;
  --pagination-btn-text: #000;
  --pagination-btn-bcg: #fff;
  --pagination-btn-border: #111;
  --pagination-arrow-left: #b4afaf;
}

[data-theme="dark"] {
  --bg-color: #202024;
  --header-white-color: #111;
  --black-text-color: #111;
  --white-color: #fff;
  --title-color: #fff;
  --accent-blue-color: #4f2ee8;
  --accent-yellow-color: #eac645;
  --skroll-color: #1119;
  --modal-signup-color: #202024;
  --modal-font-color: #f6f6f6;
  --main-font-color: #9a9a9a;
  --font-card-color: #f6f6f6;
  --light-font-color: #fff9;
  --border-color: #4f2ee866;
  --grey-text-color: #b4afaf;
  --accent-color-filter: #eac645;
  --amazon-image-filter: brightness(0) invert(1);
  --loader-book-color: #4f2ee8;
  --pagination-btn-text: #fff;
  --pagination-btn-bcg: #202024;
  --pagination-btn-border: #000;
  --pagination-arrow-left: #fff;
}

section {
