.btn-up {
  background-color: var(--accent-blue-color);
  cursor: pointer;
  z-index: 1;
  opacity: .8;
  border: 5px solid #0000;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  padding: 15px;
  transition: border-color .2s ease-in, border-radius .2s ease-in;
  position: fixed;
  bottom: 90px;
  right: 30px;
}

@media only screen and (max-width: 767px) {
  .btn-up {
    opacity: .7;
    width: 54px;
    height: 54px;
    padding: 10px;
  }
}

.btn-up > use > svg {
  fill: #fff;
  text-align: center;
  justify-content: center;
  display: flex;
}

.btn-up_hide {
  display: none;
}

.btn-up:hover {
