.categories-link {
  text-decoration: none;
  transition: color .2s ease-in, font-weight .2s ease-in, text-transform .2s ease-in;
}

.category-active, .categories-link:is(:hover, :focus) {
  text-transform: uppercase;
  color: var(--accent-color-filter);
