.auth-backdrop {
  z-index: 100;
  background: linear-gradient(329deg, #4f2ee8 40%, #7e75e8 80%, #ccece7 100%);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.auth {
  background: var(--bg-color);
  border: 2px solid #111;
  border-radius: 18px;
  position: absolute;
}

.auth-btn-close {
  cursor: pointer;
  border: none;
  border-radius: 50%;
  margin-left: auto;
  display: block;
}

.auth-icon-close {
  stroke: #111;
  margin: 0 auto;
  display: flex;
}

.auth-form {
  flex-direction: column;
  display: flex;
}

.auth-icon-input {
  background-color: var(--bg-color);
  stroke: var(--bg-color);
  fill: var(--header-white-color);
}

.auth-input-container:nth-child(2) {
  position: relative;
}

.auth-icon-input:nth-child(2) {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.auth-input-container:nth-child(3) {
  position: relative;
}

.auth-icon-input:nth-child(3) {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.auth-button-signup {
  text-align: center;
  border-radius: 40px;
}

.auth-button-container {
  text-align: center;
}

.auth-button-up, .auth-button-in {
  cursor: pointer;
  background-color: initial;
  color: var(--modal-font-color);
  opacity: .5;
  border: none;
}

