.header-switcher {
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%) center no-repeat;
  width: 1em;
  height: 1em;
  display: inline-block;
}

.header-btn-list {
  align-items: center;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  inset: 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider:before {
  content: "";
  background: linear-gradient(#fff 0%, #f7f7f7 100%) center no-repeat;
  width: 18px;
  height: 18px;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 1px;
  box-shadow: 1px 2px 4px #0000001a;
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  background: linear-gradient(#fff 0%, #f7f7f7 100%) center no-repeat;
  transform: translateX(20px);
  box-shadow: 1px 2px 4px #0000001a;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

